
const PostTgMessage = async function (formData) {

    console.log('formDataformData', formData)

    const location = `https://www.cyberial.app/`;
    // const location = 'https://localhost:3000'

    const settings = {
        method: 'POST',
        mode: 'cors', // no-cors, *cors, same-origin
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData) // body data type must match "Content-Type" header
    };

    try {
        const fetchResponse = await fetch(location, settings);
        let result = fetchResponse.status;
        var body = await fetchResponse.json(); // .json() is asynchronous and therefore must be awaited
        // console.log('resulttt', body)
        return body
    } catch (e) {
        console.log('PostTgMessage method has error', e)
        return e;
    }

}


export default PostTgMessage