
import React, { useEffect, useState } from 'react'

//

// import cardFormatter from './cardFormatter/cardFormatter'



function useInput({
    className, type, inputMode, inputEvent,
    minBankWidhdrawValue
}) {

    //
    console.log('minBankWidhdrawValue', minBankWidhdrawValue)

    const [value, setValue] = useState(
        inputEvent === 'blockchainCurrency'
            ? '5000'
            : inputEvent === 'bankCurrency' ? '9' : ''
            // 'UA633220010000026206314444032'
    );
    const [error, setError] = useState(null);
    const [errorInputValue, setErrorInputValue] = useState(undefined)
    //

    useEffect(
        () => {
            if     (inputEvent === 'blockchainCurrency' && value < 5000) setError("Min 5000 usdt");
            else if(inputEvent === 'bankCurrency'       && value < minBankWidhdrawValue) setError(`Min ${minBankWidhdrawValue} usdt`);
            else setError(null);
        }, [value]
    )

    function isValidEmail() {
        return /\S+@\S+\.\S+/.test(value);
    }

    console.log('errorInputValue', errorInputValue)

    useEffect(
        () => {
            
            if(inputEvent === 'mainInput' ) {
                if ( value.length !== 0 && !isValidEmail(value)) {
                    setErrorInputValue('Email is invalid');
                } else {
                    setErrorInputValue(null);
                }
            } 
            else if (inputEvent === 'userTelegram'){
                if (value.length < 5 && value.length !== 0) {
                    setErrorInputValue('Telegram username must be more 5 characters');
                } else {
                    setErrorInputValue(null);
                }
            }

            else if(value.length < 5 || !value){
                setErrorInputValue('Min 5 characters');
            } 
            
            else setErrorInputValue(null)
        }, [value]

    )

    //

    const input =
        <input
            className={className}
            value={
                inputEvent === 'bankCurrency' 
                    ? value
                    : value
            }
            // maxLength={+assetDataOneReserves?.decimals}
            onChange={ e => setValue( e.target.value ) }
            type={type}
            name='userNumber'
            inputMode={inputMode}
            // onBlur={() => setBlur('dadas')}
        />


    let amountDepositValue

    //

    return [
        input,
        amountDepositValue = value,
        error,
        errorInputValue,
        setErrorInputValue,
        
    ];

}



export default useInput